const SET_NAME = "SET_NAME";

let initialState = {
  user: {
    name: "",
    friends: [],
  },
  users: [],
};

const usersReducer = (state = initialState, action) => {
  if (action.type === SET_NAME) {
    return {
      ...state,
      user: {
        ...state.user,
        name: action.name,
      },
    };
  }
  return state;
};

export const addName = (name) => ({ type: SET_NAME, name });

export const registration = () => (dispatch) => {};

export default usersReducer;
